body {
  padding: 32px;
}

.btn {
  width: calc(100% - 64px);
  position: absolute;
  bottom: 32px;
  left: 32px;
  margin: 0;
}

.no-margin {
  margin: 0;
}

.emoji-containter {
  position: absolute;
  top: 45%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  width: calc(100% - 64px);
}

.emoji-containter p {
  font-size: 2.5em;
}

.emoji-containter p span {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  margin: 6px;
  cursor: pointer;
}



/* TO REMOVE (temporary) */

.btn-large, .btn-large:hover {
  font-size: 125px;
  -webkit-box-shadow: none;
          box-shadow: none;
  background: none;
  cursor: default;
}



